/**
 * @packageDocumentation
 * @module Serializer
 */
import plugins from './plugins';
import createCustomizedMoPlugins from './createCustomizedMoPlugins';
import type {
  MoSerializationPluginFactory as PluginFactory,
} from '@ali/4ever-cangjie';

/**
 * 编辑器插件创建函数。内置了编辑器的默认插件，可以额外传入一些可选的选项，来创建插件对象。
 */
const createMoPlugins = createCustomizedMoPlugins(plugins as unknown as Record<string, PluginFactory>);

export default createMoPlugins;
